.trainers {
  &__wrapper {
    padding-left: max(15px, calc((100% - var(--content-width)) / 2));
  }
  &__content {
    margin-bottom: 60px;

    @include mobile {
      margin-bottom: 0;
    }
  }

  .swiper-slide {
    width: 360px;

    @include mobile {
      width: 220px;
    }
  }
  .swiper-scrollbar-drag {
    background-color: var(--dark-color);
  }

  &__image {
    margin-bottom: 20px;
    border-radius: 20px;
    max-height: 500px;
  }

  &__name {
    font-weight: 600;
    margin-bottom: 10px;
    font-size: 24px;
    line-height: 160%;
    color: var(--dark-color);
  }

  &__post {
    margin: 0;
    margin-bottom: 20px;
    font-weight: 400;
    font-size: 18px;
    line-height: 160%;
    color: var(--text-color);
  }

  &__btn {
    font-weight: 400;
    font-size: 18px;
    line-height: 160%;
    color: var(--accent-color);
  }
  &__nav {
    @include flex-all-sb;
  }

  &__scroll {
    width: 360px;
    height: 5px;
    border-radius: 40px;
    background: rgba(20, 16, 36, 0.08);
  }

  &__slider-btn {
    @include bg-cover;

    width: 40px;
    height: 40px;

    &--prev {
      background-image: url('../img/arrow-prev.svg');
    }
    &--next {
      background-image: url('../img/arrow-next.svg');
    }

    &:not(:last-child) {
      margin-right: 10px;
    }

    @include mobile {
      display: none;
    }
  }
}
