.hero {
  min-height: 500px;
  background-color: var(--dark-color);
  padding-top: 160px;
  min-height: 800px;

  @include mobile {
    padding-top: 140px;
    padding-bottom: 40px;
  }

  &__container {
    @include flex-v-center;

    @include mobile {
      position: relative;
      flex-direction: column;
      align-items: center;
    }
  }

  &__content {
    max-width: 529px;

    @include high-tablet {
      max-width: none;
    }
  }

  &__image {
    transform: translateX(67px);

    @include high-tablet {
      display: none;
    }

    @include mobile {
      display: block;
      transform: none;
    }
  }

  &__title {
    margin: 0;
    font-weight: 600;
    font-size: 70px;
    text-transform: uppercase;
    line-height: 160%;
    color: var(--light-color);
    margin-bottom: 50px;

    @include mobile {
      font-size: 30px;
      text-align: center;
      width: 100%;
      margin-bottom: 20px;
    }

    &__text {
      text-transform: none;
      display: inline-block;
      max-width: 95%;
      font-weight: 400;
      font-size: 20px;
      line-height: 160%;

      @include mobile {
        margin-top: 10px;
        font-size: 16px;
        line-height: 26px;
      }
    }
  }

  @include high-tablet {
    padding-bottom: 100px;
    min-height: auto;
  }

  @include mobile {
    padding-bottom: 60px;
  }

  &__btn {
    @include mobile {
      position: absolute;
      bottom: 0;
      padding: 25px 54px;
      font-size: 16px;
      width: calc(100% - 30px);
    }
  }
}
